import React from 'react';
import textContext from '../../textContext';
import { useApp } from '../../context/AppContext';
import { useToken } from '../../context/TokenContext';
import { useQuery } from '@apollo/react-hooks';
import { useWallet } from '@terra-money/wallet-provider';
import * as AWS from 'aws-sdk'

// Comps
import Search from './Search';
import Invest from './Invest';

export default function InvestPanel(props) {

  const { language, handleMarket } = useApp();
  const { network } = useWallet();
  const { tokenList, market, query } = useToken();
  const text = textContext[language].investment.invest;

  const [search, setSearch] = React.useState('');
  const [tvl, setTVL] = React.useState();
  const [investData, setInvestData] = React.useState([]);
  const [investPlus, setInvestPlus] = React.useState([]);

  // let aprArray = props.deltaApr && Object.entries(props.deltaApr).map(m => { return {...m[1]}})

  const { data } = useQuery(query);
  React.useEffect(() => {
    if (tokenList !== null) {
      setInvestData(tokenList.invest);
    }
  }, [tokenList, search])

  React.useEffect(() => {
    const documentClient = new AWS.DynamoDB.DocumentClient({ region: "us-west-2" });
    const query = async () => {
      const response = await documentClient
      .scan({
        TableName: network.chainID === 'bombay-12' ?  "strategy_tvl_dev" : "strategy_tvl",
        KeyConditionExpression: "strategy_id=:strategy_id",
      })
      .promise();
      setTVL(response);
    };
    query().catch((error) => console.error(JSON.stringify(error, null, 2)) );
  }, [network])

  React.useEffect(() => {
    const investPlusData = [];

    let sortTVL = tvl && tvl.Items.sort(function(a, b) {
      return  b['timestamp_sec']-a['timestamp_sec'];
    });
    if (data && market && investPlus.length === 0) {
      Object.entries(data).forEach((d,i) => {
        let obj = {};
        obj.address = d[1].token;
        obj.title = d[1].symbol;
        obj.name = d[1].name;
        // let commission = aprArray && aprArray.filter(f => f.token1.symbol === obj.title)[0];
        handleMarket(obj).then((res) => {
          let updated = new Date() - new Date(parseInt(res) * 1000) <= 60*1000;
          obj.action = res === true ? true : updated;
          let short = Number(d[1].statistic.apr.short);
          let long = Number(d[1].statistic.apr.long);
          let anchorAPY = Number(market.markets[0].APY);
          let min_collateral = d[1].statistic.minCollateralRatio;
          let defaultCol = 1.8*Number(min_collateral);
          let APR = (anchorAPY * (defaultCol/(defaultCol + 1))) + (long * (2/(defaultCol + 1))) + (short * (1/(defaultCol + 1)));
          let APY = Math.pow((1+APR/365),365)-1;

          obj.value = d[1].prices.price;
          obj.support = ['ust'];
          obj.short = short;
          obj.long = long;
          obj.chain = 'TERRA';
          obj.minCollateral = min_collateral;
          obj.APY = APY;
          if (sortTVL && sortTVL[0] && (obj.address === sortTVL[0]['strategy_id'])) {
            obj.tvl = Number(sortTVL[0]['tvl_uusd'])/1000000;
          }
          investPlusData.push(obj)
          if (Object.entries(data).length === i+1) {
            setInvestPlus(investPlusData)
          }
        })
      })
    }

  }, [market]) // eslint-disable-line react-hooks/exhaustive-deps

  const handleSearch = (fil) => {
    setSearch(fil);
  };

  return (
    <div>
      <Search handleSearch={handleSearch} />
      <Invest
        plus
        data={investPlus}
        search={search}
        filters={text.filterPlus}
        title={text.title.investPlus}
      />
      <Invest
        data={investData}
        search={search}
        filters={text.filter}
        title={text.title.invest}
        />
    </div>
  )
}