import * as React from 'react';
import { LCDClient } from '@terra-money/terra.js';
import { useWallet } from '@terra-money/wallet-provider';

const AppContext = React.createContext();

export function useApp() {
  return React.useContext(AppContext);
}

const AppProvider = ({ children }) => {
  const [language, setLanguage] = React.useState('english');
  const [width, setwidth] = React.useState(window.innerWidth);
  const { network } = useWallet();

  React.useEffect(() => {
    // Check local storage on page load to get last saved language
    const langCheck = localStorage.getItem('language');
    if (langCheck !== null) {
      setLanguage(langCheck);
    }
    // Set primary language to english
    else {
      setLanguage('english');
    }
  }, []);

  // Set Width
  React.useEffect(() => {
    window.addEventListener('resize', (x) => setwidth(x.target.innerWidth));
  }, [width]);

  const handleMarket = async (data) => {
    const lcd = new LCDClient({ URL: network.lcd, chainID: network.chainID });
    let asset = data ? data.title : '';
    let contractAddr =
      network.chainID === 'bombay-12'
        ? 'terra1uvxhec74deupp47enh7z5pk55f3cvcz8nj4ww9'
        : 'terra1t6xe0txzywdg85n6k8c960cuwgh6l8esw6lau9';
    let defaultBaseAsset =
      network.chainID === 'bombay-12'
        ? 'terra16vfxm98rxlc8erj4g0sj5932dvylgmdufnugk0'
        : 'terra1vxtwu4ehgzz77mnfwrntyrmgl64qjs75mpwqaz';
    let baseAsset = data ? data.address : defaultBaseAsset;
    let query = { price: { quote_asset: 'uusd', base_asset: baseAsset } };
    if (asset === 'Anchor Protocol') {
      return true;
    }
    const result = await lcd.wasm.contractQuery(contractAddr, query);
    return result.last_updated_base;
  };

  // Langauge update function
  const langSelect = (lan) => {
    // Update state of language in application and local storage
    setLanguage(lan);
    localStorage.setItem('language', lan);
  };

  // Check if has access
  const setAccess = () => {
    localStorage.setItem('hasAccess', true);
  };
  const getAccess = localStorage.getItem('hasAccess');

  // Check if accepted
  const setAccept = () => {
    // Update state of language in application and local storage
    localStorage.setItem('checkDisclaimer', true);
  };
  const getAccept = localStorage.getItem('checkDisclaimer');

  // Token gas price switch
  const gasFeeName = (name) => {
    switch (name) {
      case 'uusd':
        return 'UST';
      case 'uluna':
        return 'Luna';
      case 'ukrw':
        return 'KRT';
      case 'usdr':
        return 'SDT';
      case 'umnt':
        return 'MNT';
      case 'ueur':
        return 'EUT';
      case 'ucny':
        return 'CNT';
      case 'ujpy':
        return 'JPT';
      case 'ugbp':
        return 'GBT';
      case 'uinr':
        return 'INT';
      case 'ucad':
        return 'CAT';
      case 'uchf':
        return 'CHT';
      case 'uaud':
        return 'AUT';
      case 'usgd':
        return 'SGT';
      case 'uthb':
        return 'THT';
      case 'uhkd':
        return 'HKT';
      default:
        return;
    }
  };

  return (
    <AppContext.Provider
      value={{
        width,
        language,
        setAccess,
        getAccess,
        setAccept,
        getAccept,
        langSelect,
        gasFeeName,
        handleMarket,
      }}
    >
      {children}
    </AppContext.Provider>
  );
};

export { AppProvider, AppContext };
