import React from 'react'
import textContext from '../../textContext';
import { useApp } from '../../context/AppContext';
import { useToken } from '../../context/TokenContext';
import { useWallet } from '@terra-money/wallet-provider';
import { useDelta } from '../../context/DeltaContext';
import { Link } from "react-router-dom";
import ChevronRight from '@mui/icons-material/ChevronRight';

export default function Portfolio() {

  const { language } = useApp();
  const { deltaSum, initialSum } = useDelta();
  const { status } = useWallet();
  const { parseNum } = useToken();
  const text = textContext[language].investment.portfolio;

  const histoicalCalc = () => {
    let calc = (deltaSum - initialSum)
    let percent = ((deltaSum - initialSum)/initialSum*100).toFixed(2)
    return { calc, percent }
  };

  let formatter = new Intl.NumberFormat('en-US', {
    style: 'currency',
    currency: 'USD',
  });

  return (
    <div className="wrapper">
      <span className="portClass">
        <p className="title">{text.title}</p>
        <h2>${status === 'WALLET_CONNECTED' && deltaSum ? parseNum(deltaSum) : ' - -'}</h2>
      </span>
      <span className="portClass">
        <p className="subTitle">{text.list.original}</p>
        <h3>${status === 'WALLET_CONNECTED' && initialSum ? parseNum(initialSum) : ' - -'}</h3>
      </span>
      <span className="portClass">
        <p className="subTitle">{text.list.historical}</p>
        <h3>
          {(status === 'WALLET_CONNECTED' && initialSum && deltaSum) ? formatter.format(deltaSum-initialSum) : '$ - -'}
          <span className="fontOrange">
            {status === 'WALLET_CONNECTED' && !isNaN(histoicalCalc().percent) && (deltaSum && initialSum && ` ${histoicalCalc().percent}%`)}
          </span>
        </h3>
      </span>
      <Link to='/portfolio' className="linkClass">View Details <ChevronRight /></Link>
    </div>
  )
}